var part1 = function part1(data) {
  var modules = data.split("\n").filter(function (module) {
    return module;
  });
  var fuelRequired = modules.reduce(function (total, module) {
    return total + fuelForMass(Number(module));
  }, 0);
  return fuelRequired;
};

var part2 = function part2(data) {
  var modules = data.split("\n").filter(function (module) {
    return module;
  });
  var fuelRequired = modules.reduce(function (total, module) {
    return total + fuelForModule(Number(module));
  }, 0);
  return fuelRequired;
};

module.exports = {
  part1: part1,
  part2: part2
};

var fuelForMass = function fuelForMass(module) {
  return Math.floor(module / 3) - 2;
};

var fuelForModule = function fuelForModule(module) {
  var massStillRequiringFuel = fuelForMass(module);
  var fuelForFuel = 0;

  while (massStillRequiringFuel > 0) {
    var fuelToAdd = Math.floor(massStillRequiringFuel / 3) - 2;
    massStillRequiringFuel = fuelToAdd;

    if (fuelToAdd > 0) {
      fuelForFuel += massStillRequiringFuel;
    }
  }

  return fuelForMass(module) + fuelForFuel;
};